@keyframes fade-out {
	0% {
		opacity: 1;
		visibility: visible;
	}
	100% {
		opacity: 0;
		visibility: none;
	}
}

@keyframes fade-in {
	0% {
		opacity: 0;
		visibility: none;
	}
	100% {
		opacity: 1;
		visibility: visible;
	}
}

@keyframes enlarge {
	0% {
		transform: scale(1);
	}
	100% {
		transform: scale(2);
	}
}

@keyframes shrink {
	0% {
		height: 50vh;
	}
	100% {
		height: 0;
	}
}

$animation-length: 500ms;

.shrink-animation-delayed {
	animation: shrink $animation-length $animation-length ease-out both;
}

.fade-in-delayed {
	animation: fade-in $animation-length $animation-length ease-out both;
}

.fade-out {
	animation: fade-out $animation-length ease-out both;
}

.start-animation {
	animation: fade-out $animation-length ease-out both, enlarge $animation-length ease-out;
}
